import request from '@/request/api'
/**===========基本信息接口 */
//获取资产转移的列表
export function getListApi(data = {}) {
    return request({
        url: '/asset/ShiftAction/queryList.htm',
        method: 'post',
        data: data
    })
}
//新增
export function insertApi(data = {}) {
    return request({
        url: '/asset/ShiftAction/addShift.htm',
        method: 'post',
        data: data
    })
}
//更新
export function updateApi(data = {}) {
    return request({
        url: '/asset/ShiftAction/updateShift.htm',
        method: 'post',
        data: data
    })
}
//提交
export function submitApi(data = {}) {
    return request({
        url: '/asset/ShiftAction/updateShift.htm',
        method: 'post',
        data: data
    })
}
//删除
export function deleteApi(data = {}) {
    return request({
        url: '/asset/ShiftAction/delete.htm',
        method: 'post',
        params: data
    })
}
//详情
export function getDetailsApi(id = "") {
    return request({
        url: '/asset/ShiftAction/detail.htm',
        method: 'post',
        params: { id: id }
    })
}