<template>
  <div class="add-info">
    <div class="record__baseinfo">
      <dividerTitle :title="'基本信息'"></dividerTitle>
      <editForm :formConfig="formConfig" ref="editForm" :formData="editFormData">
        <template #assetTypeName="{editFormInfo}">
          <el-input
            placeholder="请选择资产信息"
            v-model="editFormInfo.assetTypeName"
            readonly
            :disabled="pageType=='details'&&!isWorkFlowEdit"
            size="mini"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click.stop="goSeleteAsset"></el-button>
          </el-input>
        </template>
      </editForm>
    </div>
    <div class="record__attach" v-if="editFormData.status&&editFormData.status!==2">
      <dividerTitle title="流程附件"></dividerTitle>
      <attachTable
        :attachSetting="attachSetting"
        :hideBtn="!formData.workflowIsApprover"
        ref="attachFile"
      ></attachTable>
    </div>
    <div
      class="record__workflow"
      v-if="pageType == 'details'&&editFormData.status&&editFormData.status!==2"
    >
      <dividerTitle :title="'工作流'"></dividerTitle>
      <workFlow
        :currentFlowData="editFormData"
        :beforeSubmit="beforeSubmit"
        :otherParams="workFlowParams"
        @submitSuccess="workFlowSuccess"
      ></workFlow>
    </div>
    <assetSelete
      v-if="showSelete"
      :hadSeleteData="hadSeleteData"
      @changeSelete="changeSeleteAsset"
      :showSelete.sync="showSelete"
    ></assetSelete>
  </div>
</template>

<script>
import workFlow from "@/components/workFlow/index";
import assetSelete from "./assetSelete";
import { getDetailsApi, updateApi } from "@/api/itAssets/assetTransfers";
export default {
  components: { workFlow, assetSelete },
  props: {
    formData: {
      type: Object,
      default: new Object(),
    },
    pageType: {
      type: String,
      default: "",
    },
    defaultSet: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      formConfig: {
        itemArr: [
          {
            label: "申请日期",
            prop: "billDate",
            type: "date",
            required: true,
          },
          {
            label: "资产分类",
            prop: "assetTypeName",
            type: "text",
            required: true,
          },
          {
            label: "型号",
            prop: "goodsModel",
            type: "text",
            placeholder: "选择资产后自动带出",
            disabled: true,
          },
          {
            label: "原归属人",
            prop: "staffunName",
            type: "text",
            placeholder: "选择资产后自动带出",
            disabled: true,
          },
          {
            label: "接收人",
            prop: "zystaffunName",
            idsStr: "empId",
            type: "user",
            required: true,
            trigger: "change",
            methods: (list) => {
              if (list.length) {
                let [obj] = list;
                let { orgNodeCode, orgNodeName, name, code } = obj;
                this.$refs.editForm.setFormItem("depatcode", orgNodeCode);
                this.$refs.editForm.setFormItem("depatName", orgNodeName);
                this.$refs.editForm.setFormItem("confirmMan", code); //下一个审批人
                this.$refs.editForm.setFormItem("confirmManName", name); //下一个审批人
              }
            },
          },
          {
            label: "接收部门",
            prop: "depatName",
            type: "text",
            placeholder: "自动带出",
            required: true,
            disabled: true,
            trigger: "change",
          },
          {
            label: "审批人",
            prop: "confirmManName",
            type: "text",
            placeholder: "自动带出",
            required: true,
            disabled: true,
            isHide: this.pageType == "details" && this.isWorkFlowEdit,
            trigger: "change",
          },
          {
            label: "转移说明",
            prop: "remark",
            type: "textarea",
            span: 24,
            rows: 3,
          },
        ],
      },
      beforeSubmit: {
        //工作流提交前检测
        methods: (data) => {
          return this.submitBefore(data);
        },
      },
      editFormData: {},
      showSelete: false,
      hadSeleteData: [],
      //附件设置
      attachSetting: {
        title: "",
        select: false,
        billType: "asset_shift", //页面标识
        sourceId: "", //单据的ID
        btnArr: ["add", "delete", "save"],
      },
    };
  },
  created() {
    this.init();
  },
  computed: {
    //工作流额外参数
    workFlowParams() {
      let { procInstType } = this.editFormData;
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return {
          procInstType: procInstType || "",//页面类型
          assignercodes: [this.editFormData.confirmMan],//下一个审批人（流程启动后才有）
        };
      }
      return { procInstType: procInstType || "" };
    },
    //是否是流程中可编辑
    isWorkFlowEdit() {
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    init() {
      if (this.pageType == "add") {
        let defaultForm = {};
        if (this.defaultSet) {
          defaultForm = { ...this.formData };
        }
        this.editFormData = Object.assign({}, defaultForm, {
          billDate: this.$utils.getNowDay(),
        });
      } else {
        this.getDetails(this.formData.id);
      }

      if (this.pageType == "details") {
        if (!this.isWorkFlowEdit) {
          this.formConfig.itemArr.map((i) => (i.disabled = true));
        }
        this.attachSetting.sourceId = this.formData.id; //附件id
      }
    },
    //获取详情
    async getDetails(id) {
      let res = await getDetailsApi(id);
      let obj = res.data || {};

      this.editFormData = Object.assign({}, obj, {
        confirmMan: obj.empId,
        confirmManName: obj.zystaffunName,
      });
    },
    //获取表单数据
    getFormData() {
      return this.$refs.editForm.getFormData();
    },
    //点击选择资产
    goSeleteAsset() {
      if (this.pageType == "details") {
        return;
      }
      let sourceId = this.$refs.editForm.getFormItem("sourceId");
      this.hadSeleteData = [{ id: sourceId }];

      this.showSelete = true;
    },
    //确定选择资产
    changeSeleteAsset(list) {
      let [obj] = list;
      let {
        assetTypeName,
        goodsModel,
        brand,
        id: sourceId,
        empId: beforeEmpId,
        staffunName,
      } = obj;
      this.$refs.editForm.setFormItem("assetTypeName", assetTypeName);
      this.$refs.editForm.setFormItem("goodsModel", goodsModel);
      this.$refs.editForm.setFormItem("brand", brand);
      this.$refs.editForm.setFormItem("sourceId", sourceId);
      this.$refs.editForm.setFormItem("beforeEmpId", beforeEmpId); //原归属人id
      this.$refs.editForm.setFormItem("staffunName", staffunName); //原归属人
    },
    //提交前检测
    //提交前检测
    submitBefore(data) {
      //如果是起草节点
      if (this.isWorkFlowEdit) {
        return new Promise((resolve, reject) => {
          this.$refs.editForm
            .getFormData()
            .then((data) => {
              let {
                billDate,
                confirmMan,
                depatcode,
                beforeEmpId,
                empId,
                remark,
                sourceId,
                id,
              } = data;
              let params = {
                billDate,
                confirmMan,
                depatcode,
                beforeEmpId,
                empId,
                remark,
                sourceId,
                submitType: 1,
                id,
              };

              this.editFormData = Object.assign({}, data);
              updateApi(params)
                .then((res) => {
                  resolve();
                })
                .catch((e) => {
                  reject(e);
                });
            })
            .catch((e) => {
              reject("表单信息未填写完整");
            });
        });
      } else {
        return true;
      }
    },
    //工作流提交成功之后
    workFlowSuccess() {
      this.$emit("update:pageType", "list");
    },
  },
};
</script>

