<template>
  <div class="page-main-box">
    <KlTable v-bind="tableConfig" v-show="pageType == 'list'" ref="klTable"></KlTable>
    <editPage v-if="pageType !== 'list'" @goBackPage="goBackPage" :rightBtnArr="rightBtnArr">
      <addInfo ref="editForm" :pageType.sync="pageType" :formData="formData"></addInfo>
    </editPage>
  </div>
</template>

<script>
import addInfo from "./add";
import {
  getListApi,
  deleteApi,
  updateApi,
  insertApi,
} from "@/api/itAssets/assetTransfers";

export default {
  name: "assetTransfers",
  components: { addInfo },
  data() {
    return {
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getTableData();
            } else {
            }
          },
          searchArr: [
            {
              type: "el-input",
              label: "单号",
              model: "billNo",
              placeholder: "单号",
            },
            {
              type: "date",
              label: "开始日期",
              model: "beginDate"
            },
            {
              type: "date",
              label: "结束日期",
              model: "endDate"
            }
          ],
        },
        // 查询条件参数
        queryObj: {
          method: () => {
            this.getTableData();
          },
          queryData: {
            page: 1,
            pageCount: 15,
          },
        },
        tableDataObj: {
          tableArr: [],
          total: null,
        },
        operateArr: [
          {
            name: "转移",
            // isHide: !this.$store.getters["isBtnAuth"]("insert"),
            icon: "el-icon-plus",
            method: (name) => {
              this.toAdd();
            },
          },
        ],
        //  table表配置参数
        tableObj: {
          // 左边选择框展示
          selectionShow: false,
          // 左边序号展示
          // indexShow: true,
          // table表事件参数配置
          tableMethods: {
            selection: (val) => {
              this.selectionData = val;
            },
          },
          // table表展示列配置
          showItems: [
            {
              name: "单号",
              prop: "billNo",
              width: 150,
              // align: "left",
            },
            {
              name: "申请日期",
              prop: "billDate",
              width: 140,
            },
            {
              name: "资产分类",
              prop: "assetTypeName",
              width: 150,
              // align: "left",
            },
            {
              name: "资产型号",
              prop: "goodsModel",
              width: 150,
            },
            {
              name: "原归属人",
              prop: "staffunName",
              width: 120,
            },
            {
              name: "接收人",
              prop: "zystaffunName",
              width: 100,
            },
            {
              name: "接收部门",
              prop: "depatName",
              width: 150,
            },
            {
              name: "审批信息",
              prop: "stage",
              width: 200,
              scopeType: "formate",
              align: "left",
              method: (row) => {
                let { stage, auditorName } = row;
                return stage ? stage + " " + auditorName : "-";
              },
            },
            {
              name: "状态",
              prop: "status",
              scopeType: "formate",
              method: (row) => {
                let { status } = row;
                return this.$store.getters["getFlowStatus"](status);
              },
            },
            {
              name: "操作",
              prop: "operate",
              width: 150,
              scopeType: "button",
              fixed: "right",
              buttonArr: [
                // 最后一行操作栏数组
                {
                  name: "详情",
                  method: (row) => {
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return true;
                  },
                  icon: "false",
                },
                {
                  name: "审批",
                  method: (row) => {
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return row.workflowIsApprover;
                  },
                  icon: "false",
                },
                {
                  name: "编辑",
                  method: (row) => {
                    this.editInfo(row);
                  },
                  isShow: (row, that) => {
                    return row.status === 2;
                  },
                  icon: "false",
                },
                {
                  name: "删除",
                  method: (row) => {
                    this.deleteInfo(row);
                  },
                  isShow: (row, that) => {
                    // return true;
                    return row.status === 2;
                  },
                  icon: "false",
                },
              ],
            },
          ],
        },
        // 分页展示总条配置
        paginationObj: {
          pageSizes: [15, 30, 50, 100],
        },
        paginationIsshow: true,
        showklTable: false,
      },
      pageType: "list",
      formData: {},
    };
  },
  computed: {
    rightBtnArr() {
      let arr = [
        {
          text: "保存",
          icon: "el-icon-check",
          hide: this.pageType == "details",
          methods: () => {
            this.doSubmit(2);
          },
        },
        {
          text: "提交",
          icon: "el-icon-finished",
          hide: this.pageType == "details",
          methods: () => {
            this.doSubmit(1);
          },
        },
      ];
      return arr;
    },
  },
  watch: {
    pageType(val) {
      if (val == "list") {
        this.getTableData();
      }
    },
  },
  mounted() {
    this.getTableData();
  },
  methods: {
    //新建
    toAdd() {
      this.pageType = "add";
      this.formData = {};
    },
    //查询数据
    getTableData() {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      // form.pageCount=form.pageSize;
      // this.$delete(form, 'pageSize');
      getListApi(form)
        .then((res) => {
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch((res) => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    },
    //详情
    detailsInfo(row) {
      this.formData = Object.assign({}, row);
      this.pageType = "details";
    },
    //编辑
    editInfo(row) {
      this.formData = Object.assign({}, row);
      this.pageType = "edit";
    },

    //删除
    deleteInfo(row) {
      // this.$confirm(`是否删除【${row.trainingName}】`, "提示", {
      this.$confirm(`是否确认删除?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          deleteApi({ id: row.id }).then((res) => {
            this.$message.success("操作成功");
            this.getTableData();
          });
        })
        .catch((e) => {});
    },
    //点击提交
    doSubmit(submitType) {
      this.$refs.editForm
        .getFormData()
        .then((data) => {
          let {
            billDate,
            confirmMan,
            depatcode,
            beforeEmpId,
            empId,
            remark,
            sourceId,
            id,
          } = data;
          let params = {
            billDate,
            confirmMan,
            depatcode,
            beforeEmpId,
            empId,
            remark,
            sourceId,
            submitType: submitType,
            id,
          };
          //测试流程用，后注释
          // params.empId = "lidan";
          // params.confirmMan = "lidan";

          const actionApi = (queryData) => {
            return queryData.id ? updateApi(queryData) : insertApi(queryData);
          };

          actionApi(params).then((res) => {
            this.$message.success("操作成功！");
            if (submitType == 2) {
              this.pageType = "edit";
              if (res.data) {
                this.formData.id = res.data;
              }
              this.$refs.editForm.getDetails(this.formData.id);
            } else {
              this.pageType = "list";
            }
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    //返回
    goBackPage(data) {
      this.pageType = "list";
    },
  },
};
</script>

<style>

</style>