<template>
  <div class="asset-selete">
    <dialogPublic
      title="我的资产"
      :show.sync="showDialong"
      @doSubmit="doSave"
      @doClose="doClose"
      fullscreen
      appendTobody
    >
      <seleteTable :tableSetting="tableSetting" :hadData="hadSeleteData" ref="seleteTable"></seleteTable>
    </dialogPublic>
  </div>
</template>

<script>
import seleteTable from "@/components/table/seleteTable";
import { getListApi } from "@/api/itAssets/myAsset";
export default {
  props: {
    hadSeleteData: {
      type: Array,
      default: new Array(),
    },
    showSelete: {
      type: Boolean,
      default: true,
    },
  },
  components: { seleteTable },
  data() {
    return {
      tableSetting: {
        maxHeight: "600px",
        column: [
          {
            label: "编号",
            prop: "assetsNumber",
          },
          {
            label: "分类",
            prop: "assetTypeName",
          },
          {
            label: "品牌",
            prop: "brand",
          },
          {
            label: "型号",
            prop: "goodsModel",
          },
          {
            label: "所有人",
            prop: "staffunName",
          },
        ],
        data: [],
      },
      showDialong: true,
    };
  },
  created() {
    this.getAssets();
  },
  methods: {
    async getAssets() {
      let res = await getListApi();
      let { list } = res.data;
      this.$refs.seleteTable.setTableData(list || []);
    },
    doClose() {
      this.$emit("update:showSelete", false);
    },
    doSave() {
      let list = this.$refs.seleteTable.getSeleteData();
      if (list.length == 1) {
        this.$emit("changeSelete", list);
        this.doClose();
      } else {
        this.$message.warning("请选择一个需要操作的资产！");
      }
    },
  },
};
</script>

